import React, { createContext, useContext, useState } from 'react'
import axios from 'axios'
import { UserToken } from './UserToken'


export let CartContext = createContext(0)

export default function CartContextProvider({children}) {
  let [cartNumber,setCartNumber] = useState(0)
  let [cartId,setCartId] = useState(0)
  let {isLogin} = useContext(UserToken)
  let headers = {token:isLogin}
  let BaseUrl = "https://ecommerce.routemisr.com"


  // add to cart
  function addToCart(productId){
    return axios.post(`${BaseUrl}/api/v1/cart`,{productId},{headers}).then(res=>res).catch(err=>err)
  }
  // get cart
  function getCart(){
    return axios.get(`${BaseUrl}/api/v1/cart`,{headers}).then(res=>res).catch(err=>err)
  }
  // delete item
  function delCartItem(id){
    return axios.delete(`${BaseUrl}/api/v1/cart/${id}`,{headers}).then(res=>res).catch(err=>err)
  }
  // update item count
  function updateCartItem(id,count){
    return axios.put(`${BaseUrl}/api/v1/cart/${id}`,{count},{headers}).then(res=>res).catch(err=>err)
  }
  // check out
  function checkOut(id,shippingAddress){
    return axios.post(`${BaseUrl}/api/v1/orders/checkout-session/${id}`,{shippingAddress},{headers}).then(res=>res).catch(err=>err)
  }

  return <CartContext.Provider value={{addToCart,getCart,delCartItem ,updateCartItem,checkOut,cartNumber,setCartNumber,cartId,setCartId}}>
      {children}
    </CartContext.Provider>
  
}
